function fadeIn() {
  $('.fade-in-trigger').each(function() {
    const elementPos = $(this).offset().top - 50;
    const scroll = $(window).scrollTop();
    const windowHeight = $(window).height();

    if (scroll >= elementPos - windowHeight) {
      $(this).addClass('fade-in');
    }
  });
}

$(window).scroll(() => {
  fadeIn();
});
